#footer {
    position : relative;
    padding: 40px 0px 50px 0px;
    background: rgba(55, 55, 55, 1);

    display: flex;
    justify-content: center;
}

.footer-container {
    width: 90%;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.footer-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footer-logo-box {
    display: flex;
    align-items: center;
    gap: 10px;
}

.footer-logo-text {
    color: white;

    font-family: 'GimhaeGaya';
    font-size: 20px;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: -0.4px;
}

.footer-link-box {
    display: flex;
    align-items: center;
    gap: 20px;
}

.footer-email-link {
    color: white;

    font-family: 'Inter';
    font-size: 12px;
    font-weight: 400;
}

.footer-copyright {
    color: white;

    font-family: 'Inter';
    font-size: 12px;
    font-weight: 400;
}